.page-header {
  padding-top: 30px;
  padding-left: 98px;
  padding-right: 98px;

  @media (max-width: $tablet-width) {
    padding-left: 45px;
    padding-right: 43px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px;
  }

  &--open-menu {
    background-color: $accent-color;
    padding-bottom: 75px;

    @media (max-width: $mobile-width) {
padding-bottom: 44px;
    }
  }
}

.page-header__main-nav {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.page-header__button-menu {
  display: none;
  box-sizing: border-box;
  width: 25px;
  height: 18px;
  margin-top: 24px;
  margin-right: 30px;
  margin-left: -1px;
  padding: 0;
  background-color: transparent;
  border: none;
  background-image: url(../../img/menu-button-background.svg);
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;

  @media (max-width: $tablet-width) {
    display: block;
  }

  @media (max-width: $mobile-width) {
    margin-top: 17px;
  }
}

.page-header__icon-menu {
  stroke: $dark;
}

.page-header--open-menu .page-header__icon-menu {
  stroke: $white;
}

.page-header__search-form {
  display: flex;
  margin-top: 9px;

  @media (max-width: $tablet-width) {
    order: 2;
    width: 100%;
    margin-top: 22px;
    margin-bottom: 26px;
    display: none;
  }

  @media (max-width: $mobile-width) {
    margin-top: 12px;
    margin-bottom: 16px;
  }
}

.page-header__index-link {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 13px;
}

.page-header__main-logo {
  fill: $accent-color;

  @media (max-width: $mobile-width) {
    width: 52px;
    height: 52px;
  }
}

.page-header--open-menu .page-header__main-logo {
  fill: $white;
}

.page-header__user-nav {
  &--login {
    @media (max-width: $tablet-width) {
      order: 3;
      width: 100%;
      display: none;
    }
  }

  &:first-of-type {
    margin-left: 108px;

    @media (max-width: $tablet-width) {
      margin-left: 0;
      margin-top: 25px;
    }

    @media (max-width: $mobile-width) {
      margin-top: 15px;
    }
  }
}

.page-header--open-menu .page-header__user-nav {
  &--login {
    @media (max-width: $tablet-width) {
      display: block;
    }
  }
}

.page-header__site-nav {
  display: flex;
  justify-content: center;
  width: 100%;

  @media (max-width: $tablet-width) {
    display: block;
    order: 2;
    display: none;
  }
}

.page-header--open-menu .page-header__site-nav {
  display: block;
}

.page-header__about {
  display: none;

  @media (max-width: $tablet-width) {
    width: 100%;
    order: 2;
  }
}

.page-header--open-menu .page-header__about {
  display: block;
}
